<template lang="html">
  <div class="Release-message">
    <Row type="flex" justify="space-around" class="code-row-bg search_criteria">
      <Col span="12" >
        <p>{{$t('common.Release_time')}} </p>
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker> —
        <DatePicker type="date" placeholder="" style="width: 120px"></DatePicker>
        <Button type="primary" class="mgl-10">{{$t('common.Query')}}</Button>
      </Col>
      <Col span="12" style="line-height: 6">
        <Button type="primary" @click="goto('Add_message')">{{$t('common.Post_new_message')}}</Button>
      </Col>
    </Row>
    <Table highlight-row :columns="columns1" :data="data1" @on-row-click="onedata" :style="{marginTop:'20px'}"></Table>
  </div>
</template>

<script>
export default {
  name:'Release_message',
  data (){
    return {
      columns1 :[
        {
          title: this.$t('common.SID'),
          key: 'SID',
          align:'center',
          width:90
        },
        {
            title: this.$t('common.Cover'),
            key: 'cover',
            align:'center',
            render:(h,params) =>{
              return h('img',{
                attrs: {
                  src: params.row.cover
                },
                props: {

                },
                style: {
                  width:'50px'
                }
              },'')
            }
        },
        {
            title: this.$t('common.Title'),
            key: 'title',
            align:'center',
            width:200
        },
        {
            title:  this.$t('common.Release_time'),
            key: 'date',
            align:'center'
        },
        {
            title: this.$t('common.Operate'),
            key: 'operation',
            align:'center',
            render: (h, params) => {
              return h('div', [
                  h('Button', {
                      props: {
                          type: 'text',
                          size: 'small'
                      },
                      style: {
                          marginRight: '10px',
                          color:'#00CCFF'
                      },
                      on: {
                          click: () => {
                              //this.show(params.index)
                          }
                      }
                  }, this.$t('common.Delete'))
              ]);
          }
        }
      ],
      data1 :[
        {
          SID:'1',
          cover:'static/img/p1.jpg',
          title:'普通评级方法',
          date:'2018-02-03 10:30:20',
          operation:''
        },
        {
          SID:'2',
          cover:'static/img/p2.jpg',
          title:'高级评级方法',
          date:'2018-02-04 11:30:20',
          operation:''
        },
        {
          SID:'3',
          cover:'static/img/p3.jpg',
          title:'高级评级方法',
          date:'2018-02-05 12:30:20',
          operation:''
        }
      ]
    }
  },
  methods:{
    onedata(){
      this.$router.push('/Message_detail');
    },
    goto (index){
      this.$router.push('/'+index);
    }
  }
}
</script>

<style lang="css">
.Release-message{
  
}
.Release-message .search_criteria p{
  height: 30px;
}
.Release-message .search_criteria {
  background-color: #fff;
  line-height: 50px;
  text-align: center;
}
</style>



